import { useForm } from "react-hook-form";
import { Link, useNavigate } from "react-router-dom";
import styled from "styled-components";
import { PageTitle } from "../../components/PageTitle";
import { mainStyle } from "../../styles/GlobalStyled";

const Wrap = styled.section`
  width: 100%;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
`;

const TextWrap = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
  max-width: 500px;
  width: 100%;
  padding: 20px;
  form {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
  }
`;
const Logo = styled.h3`
  font-size: 30px;
  font-weight: 900;
  color: ${mainStyle.logoColor};
`;
const Title = styled.h3`
  font-size: 14px;
  font-weight: 300;
  opacity: 0.7;
  margin-top: 20px;
`;

const Input = styled.input`
  all: unset;
  width: 100%;
  border-bottom: 1px solid ${mainStyle.logoColor};
  padding: 10px 10px;
  margin-bottom: 15px;
  margin: 20px 0;
`;

const Button = styled.button`
  all: unset;
  background-color: white;
  opacity: 0.5;
  width: 100%;
  padding: 20px 10px;
  margin-top: 20px;
  color: #333;
  font-size: 22px;
  font-weight: 900;
  text-align: center;
  border-radius: 20px;
`;

const userDb = {
  dbUsername: "test",
  dbPw: "111111",
};

export const Login = () => {
  const navigate = useNavigate();
  const {
    register,
    handleSubmit,
    formState: { errors },
    getValues,
    setError,
  } = useForm({ mode: "onChange" });

  // console.log(errors);

  const onSubmit = () => {
    const { email, pw } = getValues();
    // console.log(email, pw);
    const { dbUsername, dbPw } = userDb;
    // console.log(dbUsername, dbPw);

    if (email !== dbUsername) {
      setError("idResult", { message: "  " });
    }
    if (pw !== dbPw) {
      setError("pwResult", { message: "  " });
    }
    if (email === dbUsername && pw === dbPw) {
      navigate("/");
    }
  };
  // console.log(errors);

  return (
    <>
      <PageTitle title="" />
      <Wrap>
        <TextWrap>
          <Logo>Heving</Logo>

          <form onSubmit={handleSubmit(onSubmit)}>
            <Input
              type="text"
              {...register("email", {
                required: " ",
                minLength: {
                  value: 4,
                  message: " 4  ",
                },
              })}
              placeholder=" "
            ></Input>
            {errors?.email?.message}
            {errors?.idResult?.message}
            <Input
              type="password"
              {...register("pw", {
                required: " ",
                minLength: {
                  value: 6,
                  message: " 6  ",
                },
              })}
              placeholder=" "
            ></Input>
            {errors?.pw?.message}
            {errors?.pwResult?.message}

            <Button></Button>
          </form>

          <Title>
              | <Link to={"/join_us"}></Link>
          </Title>
        </TextWrap>
      </Wrap>
    </>
  );
};
