import styled from "styled-components";

const SFooter = styled.div`
  padding: 75px 0;
  display: flex;
  justify-content: center;
  align-items: center;
  border-top: 1px solid #555;
  margin-top: 150px;
`;
export const Footer = () => {
  return (
    <SFooter>
      <span>&copy; Choi Heejeong {new Date().getFullYear()}</span>
    </SFooter>
  );
};
